<template>
    <nav class="nav container">

        <a href="" class="logo">
            <img src="@/assets/images/logo.svg" alt="">
            <h1>vue weather</h1>
        </a>

        <div class="nav__right">
            <button class="nav__right_btn" @click="switchTheme">
                <img src="@/assets/images/drop.svg" alt="">
            </button>
            <input v-model="city" type="text" placeholder="Введите город" @keydown.enter="getWeather(city), city = ''">
        </div>
    </nav>
</template>

<script>

import { mapActions } from 'vuex'

export default {
    data() {
        return {
            city: ''
        }
    },
    methods: {
        ...mapActions(['getWeather']),
        switchTheme(){
            let value = document.body.style.getPropertyValue('--bg')
            if (value == 'white') {
                document.body.style.setProperty('--bg', '#312e2ef8')
                document.body.style.setProperty('--text', 'beige')
            } else {
                document.body.style.setProperty('--bg', 'white')
                document.body.style.setProperty('--text', 'black')
            }
        }
    },

    created() {
        document.body.style.setProperty('--bg', 'white')
        document.body.style.setProperty('--text', 'black')
    },
}
</script>
