import { createStore } from 'vuex'
import axios from 'axios'

export default createStore({
  state: {
    apiKey: '6dcfdfd85f5f1e126b7ebd3cd7b3b480',
    weather: null
  },
  getters: {
  },
  mutations: {
    getWeather(state, payload) {
      state.weather = payload
    }
  },
  actions: {
    async getWeather(context, city) {

      const results =
        await axios.get(`http://api.openweathermap.org/geo/1.0/direct?q=${city},&appid=${context.state.apiKey}`)

      let { lat, lon, local_names } = results.data[0]

      if (local_names.ru.toLowerCase() == city.toLowerCase()) {

        let results2 =
          await axios.get(`https://api.openweathermap.org/data/2.8/onecall?lat=${lat}&lon=${lon}&appid=${context.state.apiKey}&exclude=minutely,hourly,alerts&units=metric&lang=ru`)

        let weatherInfo = { ...results2.data, name: city }

        context.commit('getWeather', weatherInfo)

      }


    }
  },
})
