<template>
    <div class="main__content">

        <div class="main__content_left">

            <img :src="getImg" alt="">
            <h2 class="main__content_left-title">{{ Math.round(weather.current.temp) }}°</h2>
            <h3 class="main__content_left-subtitle">Сегодня</h3>

            <p class="main__content_left-text">Время: {{ getTime }}</p>
            <p class="main__content_left-text">Город: {{ weather.name }}</p>

        </div>


        <div class="main__content_right">

            <img class="main__content_right-bg" src="@/assets/images/main_right.png" alt="">

            <div class="main__content_right-item">
                <div class="main__content_right-item_img">
                    <img src="@/assets/images/temp.svg" alt="">
                </div>
                <span>Температура</span>

                <p>{{ Math.round(weather.current.temp) }}° - ощущается как {{
                    Math.round(weather.current.feels_like) }}°</p>
            </div>

            <div class="main__content_right-item">
                <div class="main__content_right-item_img">
                    <img src="@/assets/images/pressure.svg" alt="">
                </div>
                <span>Давление </span>
                <p>{{ weather.current.pressure }} мм ртутного столба - нормальное</p>
            </div>
            <div class="main__content_right-item">
                <div class="main__content_right-item_img">
                    <img src="@/assets/images/precipitation.svg" alt="">
                </div>
                <span>Осадки</span>
                <p>{{ description }}</p>
            </div>
            <div class="main__content_right-item">
                <div class="main__content_right-item_img">
                    <img src="@/assets/images/wind.svg" alt="">
                </div>
                <span>Ветер</span>
                <p>{{ weather.current.wind_speed }} м/с юго-запад - легкий ветер</p>
            </div>


        </div>

    </div>
</template>

<script>

import { mapState } from 'vuex'
import { weatherName } from '@/icons.js'

export default {

    computed: {
        ...mapState(['weather']),

        description() {
            return this.weather.current.weather[0].description
        },

        getImg() {
            return weatherName[this.description] || weatherName['ясно']
        },

        getTime() {
            return new Date().toLocaleString('en-US', {
                timeZone: this.weather.timezone,
                timeStyle: 'short',
                hourCycle: 'h23'
            })
        }
    }

}
</script>
